<template>
  <v-container
    fluid
    :class="`
      ${themeColorClass}
      ${counterThemeColorText}
    `"
    id="nav-profile"
    v-intersect="onIntersect"
  >
    <v-row
      align="center"
      justify="center"
      no-gutters
      :style="mainStyle"
    >
      <v-col
        cols="12"
        class="text-center
        "
      >
        <transition
          name="fade"
          @after-appear="showIm = true"
          appear
          :duration="{ enter: 2000 }"
        >
        <span
          class="
            text-subtitle-2
            text-md-body-1
            display-1
            font-weight-black
          "
          v-if="showHi"
        >Hi</span>
        </transition>
        <transition
          name="fade"
          @after-enter="showName = true"
          :duration="{ enter: 2000 }"
        >
        <span
          class="
            text-subtitle-2
            text-md-body-1
            display-1
            font-weight-black
          "
          v-if="showIm"
        >, I'm</span>
        </transition>
        <transition
          name="typing"
          @after-enter="afterEnterNameEvent"
          :duration="{ enter: 5000 }"
        >
          <h1 class="text-md-h2
            text-h3
            font-weight-bold
            white--text
            "
            v-if="showName"
          >{{ name }}
          </h1>
        </transition>
        <transition
          name="fade"
          :duration="{ enter: 2000 }"
        >
        <h6 class="text-subtitle-2
          text-md-body-1
          display-1
          font-weight-black
          mt-4
        "
          v-if="showSchool"
        >
          University of Toronto, Software Engineering
        </h6>
        </transition>
      </v-col>
    </v-row>
    <transition
      name="fade"
      @after-enter="setContainerHeight"
      :duration="{ enter: 2000 }"
    >
      <ContactInfo
        v-if="showSchool"
      ></ContactInfo>
    </transition>
  </v-container>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

const ContactInfo = () => import('./ContactInfo.vue');
export default {
  components: {
    ContactInfo,
  },
  data() {
    return {
      showHi: true,
      showIm: false,
      showName: false,
      showSchool: false,
      mainStyle: { minHeight: '80vh' },
      name: '',
    };
  },
  computed: {
    ...mapState([
      'themeColorClass',
      'counterThemeColorText',
      'nameC',
      'nameAc',
    ]),
  },
  methods: {
    ...mapMutations([
      'setNameC',
      'setNameAc',
      'setNameStyle',
    ]),
    onNameIntersect(entries) {
      this.setNameC(entries[0].nameC);
    },
    onIntersect(entries) {
      this.setNameAc(entries[0].isIntersecting);
    },
    setContainerHeight() {
      this.setNameStyle({ minHeight: '60vh' });
      this.mainStyle = { minHeight: '50vh' };
    },
    afterEnterNameEvent() {
      this.name = 'TENZIN THABKHAE';
      this.showSchool = true;
    },
  },
};
</script>

